.login-container {
  width: 351px;
  margin: 0 auto;
  padding: 40px;
  background-color: #fff;
  border: 1px solid #ccc;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.login-container label {
  display: block;
  margin-bottom: 10px;
  color: #666;
}

.login-container input {
  width: 320px;
  height: 56px;
  border: none;
  border-radius: 28px;
  background-color: #f3f3f3;
  padding: 0px 25px;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 500;
} 

#login-button {
  display: block;
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 30px;
  background-color: #0a66c2;
  color: #fff;
  font-size: 18px;
  cursor: pointer;
}

#login-button:hover {
  background-color: #006397;
}
