.card-container {
    width: 100%;
    margin-bottom: 20px;
    padding: 20px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 3px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  }
  
  .card-container a {
    text-decoration: none;
    color: #0a66c2;
  }
  
  .card-container h2 {
    margin: 0;
    font-size: 24px;
    font-weight: normal;
  }
  
  .card-container:hover {
    background-color: #f3f2ef;
  }
  